import React, { FunctionComponent } from "react";

import Layout from "components/layout";
import SpacerRow from "components/rows/SpacerRow";
import Hero from "components/learningCenter/hero";
import { path } from "ramda";
import Title from "components/learningCenter/title";
import Paragraph from "components/learningCenter/paragraph";
import ListItemsHorizontal from "components/learningCenter/listItemsHorizontal";
import FullWidthImage from "components/learningCenter/fullWidthImage";
import SmallerImage from "components/learningCenter/smallerImage";
import ListItemsVertical from "components/learningCenter/listItemsVertical";
import ExternalLinks from "components/learningCenter/externalLinks";
import Footer from "components/learningCenter/footer";
import PullQuote from "components/learningCenter/pullQuote";

import colors from "styles/colors";

import {
  ListItemsProps,
  TextProps,
  FullWidthImageProps,
  SmallerImageProps,
  ExternalLinksProps,
  FooterProps,
} from "./types";

type ContentProps = {
  id: string;
  data: Object;
  fields: ListItemsProps &
    TextProps &
    FullWidthImageProps &
    SmallerImageProps &
    ExternalLinksProps &
    FooterProps;
};

interface Props {
  location: Location;
  pageContext: {
    contentfulData: {
      heroReference: {
        fields: {
          title: string;
          subtitle: string;
          ctaSubtitle: string;
          image: Object;
          imgAlt: string;
        };
      };
      metaTitle: string;
      metaDescription: string;
      contentReference: Array<ContentProps>;
      content: ContentProps;
    };
  };
  id: string;
  metaTitle?: string;
  indexPage: boolean;
}

const buildContentSection: FunctionComponent<ContentProps> = (data) => {
  switch (path(["sys", "contentType", "sys", "id"], data)) {
    case "listItemWithIcon":
      return (
        <>
          {data.fields.position === "horizontal" ? (
            <ListItemsHorizontal {...data.fields} />
          ) : (
            <ListItemsVertical {...data.fields} />
          )}
        </>
      );
    case "sectionTitle":
      return <Title title={data.fields.text} />;
    case "bodyText":
      return <Paragraph paragraph={data.fields.text} />;
    case "fullWidthImage":
      return (
        <FullWidthImage
          fullWidthImage={data.fields.fullWidthImage}
          imgAlt={data.fields.imgAlt}
        />
      );
    case "smallerImage":
      return (
        <SmallerImage
          smallerImage={data.fields.smallerImage}
          imgAlt={data.fields.imgAlt}
        />
      );
    case "externalLink":
      return <ExternalLinks {...data.fields} />;
    case "fourStepTile":
      return <Footer articleLinks={data.fields.articleLinks} />;
    case "pullQuote":
      return <PullQuote title={data.fields.text} />;
    default:
      return null;
  }
};

const LearningCenterTemplate = ({
  pageContext: { contentfulData },
  location,
  indexPage,
}: Props) => (
  <Layout
    location={location}
    title={contentfulData.metaTitle}
    description={contentfulData.metaDescription}
    whiteContentBackground
    whiteGutterBackground
    indexPage={indexPage}
  >
    {contentfulData.heroReference && (
      <Hero
        gutterBackground={colors.black}
        heroData={contentfulData.heroReference.fields}
      />
    )}
    <SpacerRow />
    {contentfulData.contentReference &&
      contentfulData.contentReference.length > 0 && (
        <>
          {contentfulData.contentReference.map((content) =>
            buildContentSection(content),
          )}
        </>
      )}
  </Layout>
);
export default LearningCenterTemplate;
