import React from "react";
import ReactMarkdown from "react-markdown";
import { SectionWrapper, Paragraph } from "./elements";

interface Props {
  paragraph: string;
}

const ParagraphWrapper = ({ paragraph }: Props) => (
  <SectionWrapper>
    <Paragraph>
      <ReactMarkdown source={paragraph} />
    </Paragraph>
  </SectionWrapper>
);

export default ParagraphWrapper;
