import React from "react";
import { path } from "ramda";
import { SectionWrapper, BigImage } from "./elements";
import { FullWidthImageProps } from "components/learningCenter/types";

const FullWidthImage: React.SFC<FullWidthImageProps> = ({
  fullWidthImage,
  imgAlt,
}) => (
  <SectionWrapper>
    <BigImage
      src={path(["fields", "file", "url"], fullWidthImage)}
      alt={imgAlt}
    />
  </SectionWrapper>
);

export default FullWidthImage;
