import React from "react";
import { Row, Column } from "components/Grid";
import { path } from "ramda";

import {
  TitleWrapper,
  ContentWrapper,
  Content,
  Image,
  Container,
  Learning,
  SubTitle,
  CTATitle,
  ImageWrapper,
} from "./elements";

interface Props {
  heroData: {
    title: string;
    subtitle: string;
    ctaSubtitle: string;
    image: Object;
    imgAlt: string;
  };
  gutterBackground: string;
}

const SimpleHero = ({ heroData }: Props) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={11} spanMedium={12}>
        <ContentWrapper>
          <Content>
            <Learning>learning center</Learning>
            <TitleWrapper>{heroData.title}</TitleWrapper>
            <SubTitle>{heroData.subtitle}</SubTitle>
            <CTATitle>{heroData.ctaSubtitle}</CTATitle>
          </Content>
          <ImageWrapper>
            {heroData.image && (
              <Image
                src={path(["image", "fields", "file", "url"], heroData)}
                alt={heroData.imgAlt}
                fullHeightImg
              />
            )}
          </ImageWrapper>
        </ContentWrapper>
      </Column>
    </Row>
  </Container>
);

export default SimpleHero;
